.button {
  text-align: center;
  width: 180px;
  max-width: 320px;
  padding: 15px 10px;
  background-color: #d0992e;
  border-radius: 5px;
  transition: all 0.4s ease-in-out;
  cursor: pointer;
  margin: 1rem auto;
  border: 1px solid transparent;

  a {
    font-weight: bold;
    color: #000;
    width: 100%;
    height: 100%;
    display: block;
  }

  &:hover {
    background-color: transparent;
    border-color: #d0992e;
  }
}
