.tiles {
  position: relative;

  > div {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 2rem 0;
  }

  .tile-image {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }

  .tiles-title {
    padding: 0 1rem;
  }

  @media (min-width: 990px) {
    .tiles-title {
      max-width: 1170px;
      margin: 0 auto;
    }
  }

  .theme-1 {
    display: none;

    @media (min-width: 990px) {
      animation: fadeIn ease 2s;
      -webkit-animation: fadeIn ease 2s;
      -moz-animation: fadeIn ease 2s;
      -o-animation: fadeIn ease 2s;
      -ms-animation: fadeIn ease 2s;
      display: flex;
      width: 100%;
      padding: 0;
      position: absolute;
      top: -230px;
      border-top: 1px solid rgba(255, 255, 255, 0.1);

      .tile {
        max-width: 300px;
        padding: 1rem;
        flex: 1 1 20%;

        &-image {
          width: 90px;
          height: 90px;
          border-radius: 80px;
          margin: 1rem auto;
        }
      }

      h3 {
        margin: 0.4rem 0;
        line-height: 1;
      }

      p {
        margin: 0.4rem 0;
        line-height: 1;
        font-weight: 400;
      }

      p,
      h3 {
        color: rgba(255, 255, 255, 0.8);
        text-align: center;
      }
    }
  }

  .theme-2 {
    padding: 0 1rem 1rem;

    .fadeInUp {
      animation-name: fadeInUp;
      -webkit-animation-name: fadeInUp;
      animation-duration: 1s;
      animation-fill-mode: both;
      -webkit-animation-duration: 1s;
      -webkit-animation-fill-mode: both;
    }

    .tile {
      text-align: left;
      opacity: 0;

      &-text {
        padding: 1rem;

        > div {
          height: 100%;
          display: flex;
          flex-direction: column;
        }

        .button {
          margin-top: auto;
          margin-bottom: 1rem;
          width: calc(100% - 1rem);
        }
      }

      &-image {
        width: 100%;
        height: 250px;
        margin: 0 auto;
      }

      &-text {
        border: 1px solid #e6e6e6;
      }

      &-heading {
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        p {
          flex: 1 1 30%;
          text-align: right;
          color: #d0992e;
          font-weight: bold;
        }

        h3 {
          flex: 1 1 70%;
        }
      }
    }

    @media (min-width: 990px) {
      max-width: 1170px;
      margin: 0 auto;
      padding: 0;

      .tile {
        display: flex;
        flex-direction: row;
        width: 50%;
        box-sizing: border-box;
        border: 0;

        &:nth-of-type(n + 3) {
          flex-direction: row-reverse;
          text-align: right;
        }

        .button {
          width: calc(100% - 2rem);
        }

        &-image {
          flex: 1 1 50%;
        }

        &-text {
          flex: 1 1 50%;
          padding: 0;

          > div {
            padding: 0 1rem;
            margin: auto 0;
          }
        }
      }
    }
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeInUp {
  from {
    transform: translate3d(0, 40px, 0);
  }

  to {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}
